}

.display-block {
	display: block;
}

.display-none {
	display: none;
}

.display-inline-block {
	display: inline-block;
}

.valign-middle {
	vertical-align: middle;
}

.overflow-hidden {
	overflow: hidden;
}

.visually-hidden {
	visibility: hidden;
}

.opacity-none {
	opacity: 0;
}

.fluid {
	display: block;
	width: 100%;
	height: auto;
}

.clearfix::after,
.clearfix::before {
	content: "";
	display: table;
}

.clearfix::after {
	clear: both;
}

.float-left {
	float: left;
}

.float-right {
	float: right;
}

.position-fixed {
	position: fixed;
}

.position-relative {
	position: relative;
}

.position-absolute {
	position: absolute;
}

.margin-center {
	margin: 0 auto;
}

.background-white {
	background-color: #fff;
}

.border-white {
	border-color: #fff;
}

.background-cover {
	background-size: cover;
	background-position: center;
}

.menu-inline {
	list-style-type: none;
	margin: 0;
	padding: 0;
}

.menu-inline li {
	display: inline-block;
}

.embed-container {
	position: relative;
	padding-bottom: 56.25%;
	height: 0;
	overflow: hidden;
	max-width: 100%;
}

.embed-container > iframe,
.embed-container > object,
.embed-container > embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

